.main-file-explorer{
    margin-top: 100px;
    margin: auto;
    width: 40%;
    border: 3px solid green;
    padding: 10px;
  }
  
  
.folder {
    margin-top: 6px;
    background-color: lightgray;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2px;
    width: 500px;
    cursor: pointer;
}

.inputContainer{
    display: flex;
    align-items: center;
    gap: 5px;
}

.inputContainer>span{
    margin-top: 5px;
}

.inputContainer__input {
    margin: 6px 0 0px 0;
    padding: 5px;
    display: flex;
    border: 1px solid lightgray;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
}

.folder > span {
    margin: 0 5px;
}

.folder > div > button {
    font-size: 15px;
    background-color: white;
}

.file {
    margin-top: 5px;
    padding-left: 5px;
    display: flex;
    flex-direction: column;
}